@import "variables.scss";

@font-face {
  font-family: 'NeueMachina';
  src: url('../fonts/NeueMachina-Medium.eot');
  src: url('../fonts/NeueMachina-Medium.eot?#iefix') format('embedded-opentype'),
  url('../fonts/NeueMachina-Medium.woff') format('woff'),
  url('../fonts/NeueMachina-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
}

body {
  min-width: 320px;


  font-family: $font-family;
  letter-spacing: -0.02em;


  &.overfl {
    overflow: hidden;
    height: 100vh;
  }
}

.wrapper {
  overflow-x: hidden;

}

div,
span,
a,
input,
textarea,
select,
button,
.button {
  &:focus {
    outline: none;
  }
}

ul {
  li {
    list-style: none;
  }
}

a {
  cursor: pointer;

  &:hover {
    text-decoration: none;
  }
}

.container {
  max-width: 100%;

  @media screen and(min-width: 1450px) {
    max-width: 1450px;

    .header & {
      max-width: 100%;
      padding-left: 35px;
      padding-right: 35px;
    }
  }
}

h2 {
  display: block;

  font-size: 30px;
  line-height: 140%;
  text-align: center;
  text-transform: uppercase;
  color: $black2;
}

.button {
  border: none;
  box-shadow: none;
  cursor: pointer;
  transition: 0.3s;
  text-decoration: none;

  &-primary {
    display: inline-block;
    padding: 20px 40px;

    background: linear-gradient(#6E29F6, #6E29F6);
    border-radius: 1px;

    position: relative;

    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: $white;
    overflow: hidden;

    span {
      position: relative;
      z-index: 2;
    }


    &:after {
      content: '';
      width: 100px;
      height: 100px;
      display: block;

      position: absolute;
      top: 0;
      left: 35%;
      z-index: 1;

      background: linear-gradient(30.52deg, #00F0FF 7.17%, rgba(57, 93, 239, 0.1) 97.27%);
      opacity: 0.7;
      filter: blur(10px);
      transform: rotate(-38.99deg);
    }

    &:hover {
      background: linear-gradient(274.98deg, #621AF0 0%, #884BFF 66.29%);
      color: $white;
    }
  }


  &-mob {
    display: none;
  }

  &-center {
    margin: 37px 0 20px;
    text-align: center;
  }
}

.link-mob {
  display: none;
}

.bg {
  width: 100%;

  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;

  &:before {
    content: '';
    width: 249px;
    height: 422px;
    display: block;

    position: relative;
    left: 0;
    top: 270px;
    z-index: -1;

    background-image: url("../images/bg3.png");
  }

  &:after {
    content: '';
    width: 855px;
    height: 1355px;
    display: block;

    position: absolute;
    left: 0;
    top: 270px;
    z-index: -1;

    background-image: url("../images/bg4.png");
  }
}

.header{
  padding: 30px 0;

  &__toggler {
    display: none;
  }

  &__mob-soc,
  &__mob-mail,
  &__mob-list {
    display: none;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__right,
  &__left {
    display: flex;
    align-items: center;
  }

  &__left {
    width: 100%;
    justify-content: space-between;
  }

  &__logo {
    display: inline-block;
    margin-right: 135px;
    margin-top: 7px;
  }

  &__menu {
    margin-top: 5px;

    ul {
      display: flex;
      align-items: center;
      padding: 0;
      margin: 0;

      li {
        padding: 0 22px;

        line-height: 0;

        a {
          display: inline-block;

          position: relative;

          font-size: 18px;
          line-height: 21px;
          color: $black;
          overflow: hidden;

          &:before {
            content: '';
            position: absolute;
            display: block;
            bottom: 0;
            left: -110%;
            width: 100%;
            height: 1px;
            transition: .3s ease-in-out;
            background-color: $black;
            opacity: 0;
          }

          &:hover {
            &:before {
              transition: .3s;
              left: 0;
              opacity: 1;
            }
          }
        }
      }
    }
  }

  &__phone {
    display: inline-block;
    margin-right: 25px;
    margin-left: 20px;

    font-family: $font-family-tit;
    font-weight: 500;
    font-size: 24px;
    line-height: 23px;
    color: $black;
    transition: 0.3s;

    &:hover {
      color: $active;
    }
  }

  &__soc {
    width: 48px;
    height: 48px;
    margin-left: 15px;
    display: inline-flex;
    align-items: center;
    justify-content: center;

    background: $white;
    box-shadow: 0px 7px 29px -7px rgba(65, 32, 107, 0.17);
    border-radius: 1px;
    transition: 0.3s;

    svg {
      width: 32px;
      height: 32px;

      use {
        color: $black2;
      }
    }

    &:hover {
      box-shadow: 0px 7px 29px -7px rgba(65, 32, 107, 0.4);
    }
  }

  &__btn {
    margin-left: 15px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 48px;
    padding: 7px 33px 7px 22px;

    background: $white;
    box-shadow: 0px 7px 29px -7px rgba(65, 32, 107, 0.17);
    border-radius: 1px;
    font-size: 18px;
    line-height: 21px;
    color: $black;
    cursor: pointer;
    transition: 0.3s;

    svg {
      width: 24px;
      height: 22px;
      margin-right: 10px;

      use {
        color: $black2;
      }
    }

    &:hover {
      color: $black;
      box-shadow: 0px 7px 29px -7px rgba(65, 32, 107, 0.4);
    }
  }
}

.screen {
  height: 100vh;

  position: relative;

  overflow: hidden;

  &:before {
    content: '';
    width: 845px;
    height: 679px;
    display: block;

    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;

    background-image: url("../images/bg2.png");
  }
}

.banner {
  padding: 50px 0;
  height: calc(100vh - 270px);

  .container {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__inner {
    text-align: center;

    h1 {
      margin-bottom: 20px;

      font-size: 54px;
      line-height: 140%;
      text-transform: uppercase;
      color: $black2;
      font-weight: 400;


      br {
        &:last-of-type {
          display: none;
        }
      }

      img {
        margin-left: 5px;
        display: inline-block;
        vertical-align: top;
        margin-top: 5px;

        &:first-of-type {
          margin-left: 15px;
        }
      }
    }

    p {
      margin-bottom: 60px;

      font-size: 18px;
      line-height: 21px;
      color: $black2;
    }
  }
}

.awards {
  padding: 0 0 50px;

  &__item {
    position: relative;

    text-align: center;

    &:before {
      content: '';
      width: 25px;
      height: 51px;
      display: block;

      position: absolute;
      left: 17px;
      top: -7px;

      background-image: url("../images/svg/img1.svg");
      background-size: 25px 51px;
      background-repeat: no-repeat;
    }
    &:after {
      content: '';
      width: 25px;
      height: 51px;
      display: block;

      position: absolute;
      right: 17px;
      top: -7px;

      background-image: url("../images/svg/img2.svg");
      background-size: 25px 51px;
      background-repeat: no-repeat;
    }

    p {
      margin-bottom: 0;

      font-size: 12px;
      line-height: 120%;
      color: $black2;
    }
  }

  &__position {
    margin-bottom: 8px;
    padding: 0 45px;

    font-weight: 700;
    font-size: 24px;
    line-height: 23px;
    color: $black;
    font-family: $font-family-tit;
  }

  &__title {
    margin-bottom: 13px;
    padding: 0 45px;

    font-weight: 700;
    font-size: 12px;
    line-height: 12px;
    color: $black2;
    font-family: $font-family-tit;
  }
}

.case {
  margin-bottom: 40px;

  &__ln {
    width: 100%;
    height: 100%;

    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
  }

  &__item {
    display: block;
    padding: 0 55px 0 50px;

    position: relative;
    transition: 0.3s;
    overflow: hidden;

    &-color1 {
      background: linear-gradient(95.86deg, #FFDFED 0.22%, #FFCDE4 100%);
    }
    &-color2 {
      background: linear-gradient(96.09deg, #F4F4F4 0.31%, #F1F1F1 100%);
    }
    &-color3 {
      background: linear-gradient(96.09deg, #212121 0.31%, #121212 100%);
    }
    &-color4 {
      background: linear-gradient(96.09deg, #FEDEDB 0.31%, #FFCAC6 100%);
    }
    &-color5 {
      background: linear-gradient(96.09deg, #2BA6E8 0.31%, #2796D2 100%);
    }
    &-color6 {
      background: linear-gradient(96.09deg, #015BA1 0.31%, #00477F 100%);
    }
    &-color7 {
      background: linear-gradient(96.09deg, #E8F7FB 0.31%, #BDDBE3 100%);
    }
    &-color8 {
      background: linear-gradient(96.09deg, #303D4E 0.31%, #21272D 99.73%);
    }
    &-color9 {
      background: linear-gradient(96.09deg, #F7F7F7 0.31%, #E0E0E0 100%);
    }

    .row {
      position: relative;
      z-index: 2;

      align-items: flex-end;
    }

    &:after {
      content: '';
      width: 100%;
      height: 100%;
      opacity: 0;
      display: block;

      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;

      background: rgba(0,0,0,0.05);
      transition: 0.3s;
    }

    &:hover {
      &:after {
        opacity: 1;
      }

      .case__img {
        img {
          transform: scale(1.05);
        }
      }
    }
  }

  &__info {
    padding: 40px 0;
  }

  &__top {
    display: flex;
    align-items: center;
    margin-bottom: 90px;
  }

  &__text {
    margin-left: 50px;

    font-size: 16px;
    line-height: 160%;

    .case__item-color1 & {
      color: $colo1;
    }
    .case__item-color2 & {
      color: $color2;
    }
    .case__item-color3 & {
      color: $white;
    }
    .case__item-color4 & {
      color: $black2;
    }
    .case__item-color5 & {
      color: $white;
    }
    .case__item-color6 & {
      color: $white;
    }
    .case__item-color7 & {
      color: $black2;
    }
    .case__item-color8 & {
      color: $white;
    }
    .case__item-color9 & {
      color: $black2;
    }
  }

  &__progress {
    display: flex;
    align-items: flex-start;
    margin-bottom: 40px;

    &-item {
      margin-right: 50px;
    }

    &-numb {
      margin-bottom: 10px;

      font-size: 36px;
      line-height: 36px;
      font-weight: 500;
      color: $black2;

      .case__item-color3 & {
        color: $white;
      }
      .case__item-color5 & {
        color:  $white;
      }
      .case__item-color6 & {
        color: $white;
      }
      .case__item-color8 & {
        color: $white;
      }
    }

    &-tx {
      font-weight: 600;
      font-size: 14px;
      line-height: 120%;
      color: $black2;
      text-transform: uppercase;

      .case__item-color3 & {
        color: $white;
      }
      .case__item-color5 & {
        color:  $white;
      }
      .case__item-color6 & {
        color: $white;
      }
      .case__item-color8 & {
        color: $white;
      }
    }
  }

  &__list {
    padding: 0;
    margin: 0 0 20px;

    li {
      padding: 0;
      display: inline-block;

      position: relative;

      font-size: 16px;
      line-height: 160%;
      color: $black2;

      .case__item-color3 & {
        color: $white;
      }
      .case__item-color5 & {
        color:  $white;
      }
      .case__item-color6 & {
        color: $white;
      }
      .case__item-color8 & {
        color: $white;
      }

      &:after {
        content: '';
        margin: 0 7px 0 11px;
        display: inline-block;
        width: 4px;
        height: 4px;
        vertical-align: middle;

        border-radius: 10px;

        .case__item-color1 & {
          background: $colo1-1;
        }
        .case__item-color2 & {
          background: $color2-2;
        }
        .case__item-color3 & {
          background: $color3;
        }
        .case__item-color4 & {
          background: $color4;
        }
        .case__item-color5 & {
          background: $color5;
        }
        .case__item-color6 & {
          background: $color6;
        }
        .case__item-color7 & {
          background: $color7;
        }
        .case__item-color8 & {
          background: $color8;
        }
        .case__item-color9 & {
          background: $color9;
        }
      }

      &:last-of-type {
        &:after {
          display: none;
        }
      }
    }
  }

  &__link {
    margin: 0;
    padding: 0;

    position: relative;
    z-index: 6;

    li {
      margin: 0 25px 0 0;
      padding: 0;

      display: inline-block;
      vertical-align: middle;

      a {
        font-size: 18px;
        line-height: 160%;
        font-weight: 700;

        .case__item-color1 & {
          color:  $colo1;
        }
        .case__item-color2 & {
          color:  $color2;
        }
        .case__item-color3 & {
          color: $color3;
        }
        .case__item-color4 & {
          color: $color4;
        }
        .case__item-color5 & {
          color: $color5;
        }
        .case__item-color6 & {
          color: $color6;
        }
        .case__item-color7 & {
          color: $color7;
        }
        .case__item-color8 & {
          color: $color8;
        }
        .case__item-color9 & {
          color: $color9;
        }

        &:hover {
          opacity: 0.8;
        }
      }
    }
  }

  &__img {
    padding-top: 60px;
    transition: 0.3s;

    img {
      transition: 0.3s;
    }
  }

  &__btn {
    display: block;
    width: 100%;
    margin: 50px 0;
    padding: 19px;

    background: $gray4;
    font-weight: 500;
    border-radius: 1px;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: $black2;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      background: $gray2;
    }
  }
}

.brands {
  margin: 80px 0;

  overflow: hidden;

  &__btn {
    display: none;
  }

  &__paroller{
    width: 100%;

    &-inner {
      height: 513px;
      width: 100%;

      background-repeat: repeat-x;
      background-position: center;
      background-size: cover;
    }
  }

  &__inner {
    &-mob {
      display: none;
    }
  }
}

.technology {
  h2 {
    margin-bottom: 50px;
  }

  .col-md-6 {
    margin-bottom: 20px;
  }

  &__item {
    height: 100%;
    min-height: 186px;
    padding: 25px 35px 30px;

    position: relative;

    background: $gray;
    text-align: left;
    overflow: hidden;
    transition: 0.3s;

    &.hov {
      .technology__hov {
        opacity: 1;
        z-index: 1;
      }
    }

    &-img {
      img {
        position: absolute;
        right: 20px;
        top: 25px;
        z-index: 3;
      }
    }

    p {
      margin-bottom: 0;

      position: relative;
      z-index: 5;

      font-size: 18px;
      line-height: 160%;
      color: $black2;
    }
  }

  &__hov {
    width: 284px;
    height: 284px;
    display: block;
    z-index: -1;
    opacity: 0;

    position: absolute;
    top: 0;
    left: 0;
    transition: 0.3s;

    span {
      width: 284px;
      height: 284px;
      display: block;

      transition: transform 800ms ease-out;
      will-change: transform;
      pointer-events: none;

      border-radius: 1000px;
      background: linear-gradient(30.52deg, #01F6F7 33.29%, rgba(110, 41, 246, 0.53) 97.27%);
      filter: blur(50px);
    }
  }

  &__title {
    margin-bottom: 13px;

    position: relative;
    z-index: 5;

    font-size: 20px;
    line-height: 140%;
    color: $black2;
    font-weight: 500;
  }


}

.offer {
  margin: 60px 0;

  h2 {
    margin-bottom: 50px;
  }

  &__inner {
    width: 1050px;
    margin: auto;

    text-align: center;
  }

  &__item {
    display: inline-block;
    padding: 20px 30px;
    margin: 0 10px 25px;

    border-radius: 10px;
    font-size: 18px;
    line-height: 160%;
    color: $black2;
    font-weight: 500;

    &_color1 {
      background: $bg1;
    }
    &_color2 {
      background: $bg10;
    }
    &_color3 {
      background: $bg3;
    }
    &_color4 {
      background: $bg4;
    }
    &_color5 {
      background: $border;
    }
    &_color6 {
      background: $bg5;
    }
    &_color7 {
      background: $bg6;
    }
    &_color8 {
      background: $bg7;
    }
    &_color9 {
      background: $bg8;
    }
    &_color10 {
      background: $bg9;
    }
  }
}

.consultation {
  margin: 80px 0 0;
  padding: 75px 0 100px;

  position: relative;

  background: $gray;
  overflow: hidden;

  &:after {
    content: '';
    width: 855px;
    height: 855px;

    position: absolute;
    left: 0;
    top: 0;

    background-image: url("../images/bg1.png");
    background-repeat: no-repeat;
    background-position: left bottom;
    background-size: 100% 100%;
  }

  &__title {
    padding: 15px 40px 15px 0;

    position: relative;
    z-index: 2;

    font-size: 30px;
    line-height: 140%;
    color: $black2;
  }
}

.form {
  position: relative;
  z-index: 5;

  p {
    margin: 20px 0 0 10px;

    font-size: 18px;
    line-height: 21px;
    color: $gray3;

    a {
      color: $gray3;

      &:hover {
        color: $active;
      }
    }
  }

  .button {
    width: 100%;
  }

  &__row {
    &_flex {
      display: flex;

      .form__cell {
        width: 50%;
      }
    }
  }

  &__cell {
    padding: 10px;
    margin-bottom: 10px;

    input, textarea {
      width: 100%;
      padding: 5px 25px;

      background: $white;
      border: 1px solid $gray2;
      border-radius: 1px;
      font-size: 18px;
      line-height: 21px;
      color: $black2;
      transition: 0.3s;

      &:focus,
      &:hover {
        box-shadow: 0px 19px 33px -20px rgba(23, 13, 36, 0.17);
      }

      &::-webkit-input-placeholder {
        color: $gray3;
      }

      &:-ms-input-placeholder {
        color: $gray3;
      }

      &::placeholder {
        color: $gray3;
      }
    }

    input {
      height: 60px;
    }

    textarea {
      padding: 20px 25px;
      height: 180px;

      resize: none;
    }
  }
}

.footer {
  &__top,
  &__bottom {
    &-inner {
      padding: 30px 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  &__bottom {
    border-top: 1px solid $border;
  }

  &__menu {
    margin-left: -22px;

    ul {
      display: flex;
      align-items: center;
      padding: 0;
      margin: 0;

      li {
        padding: 0 22px;

        line-height: 0;

        a {
          display: inline-block;

          position: relative;

          font-size: 18px;
          line-height: 21px;
          color: $black;
          overflow: hidden;

          &:before {
            content: '';
            position: absolute;
            display: block;
            bottom: 0;
            left: -110%;
            width: 100%;
            height: 1px;
            transition: .3s ease-in-out;
            background-color: $black;
            opacity: 0;
          }

          &:hover {
            &:before {
              transition: .3s;
              left: 0;
              opacity: 1;
            }
          }
        }
      }
    }
  }

  &__news {
    width: 355px;
    display: flex;
    align-items: center;

    &-title {
      margin-right: 35px;
      font-size: 18px;
      line-height: 21px;
      color: $black2;
    }

    &-item {
      display: inline-block;
      height: 32px;
      margin-right: 30px;

      svg {
        width: 32px;
        height: 32px;

        use {
          color: $black2;
          transition: 0.3s;
        }
      }

      &:hover {
        svg {
          use {
            color: $active;
          }
        }
      }
    }
  }

  &__logo {
    display: inline-block;
  }

  &__contacts {
    width: 355px;

    &-mail {
      display: inline-block;
      margin-bottom: 13px;

      font-size: 18px;
      line-height: 21px;
      color: $black2;
      transition: 0.3s;

      &:hover {
        color: $active;
      }
    }

    &-all {
      display: flex;
      align-items: center;
    }

    &-tel {
      margin-right: 27px;

